/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import styled from "@emotion/styled";
import { useState } from "react";

const Container = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
  margin-bottom: 20px;
  background: #f0f5ff;
  font-family: "Shadows Into Light", serif;
`;

const Header = styled.div`
  width: 100%;
  position: relative;
  padding: 10px;
  border-radius: 3px;
  background: ${(props) => (props.clicked ? "#fff7e6" : "#fff")};
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  cursor: pointer;
`;

const DPTitle = styled.div`
  margin: 10px 5px;
  font-weight: bolder;
`;

const DPTableContainer = styled.div`
  padding: 10px;
  display: flex;
  justify-content: center;
  align-content: center;
`;

const Box = styled.div`
  min-width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid black;
  margin: 18px 8px;
  position: relative;
  background: ${(props) => (props.clicked ? "#ffe7ba" : "#fff2e8")};

  &:before {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -100%);
    line-height: 14px;
    font-size: 12px;
    font-weight: bold;
    content: "${(props) => props.name}";
  }
`;

const Ellipsis = styled.div`
  line-height: 40px;
  font-size: 24px;
  margin: 15px 10px;
`;

const Line = styled.p`
  margin: 10px 0;
  font-size: 15px;
  line-height: 18px;
`;

const EmSpan = styled.span`
  color: #002766;
  font-weight: bolder;
`;

const DPExplain = ({
  transferTable,
  transferEquation,
  baseTable,
  baseCase,
  desc,
  transferTableOther,
  baseC,
}) => {
  const [state, setState] = useState(false);
  console.log(baseC);
  const renderDPTale = (base, transfer) => (
    <DPTableContainer>
      {base
        ? base.map((item, index) => (
            <Box
              style={{ cursor: "pointer" }}
              clicked={!state}
              name={index}
              onClick={() => setState(false)}
            >
              {item}
            </Box>
          ))
        : null}
      <Ellipsis>...</Ellipsis>
      {transfer
        ? transfer.map((item, index) => (
            <Box
              style={{
                border: "2px solid #bfbfbf",
                cursor: "pointer",
              }}
              clicked={state}
              name={"i" + (index - transfer.length)}
              onClick={() => setState(true)}
            >
              {item}
            </Box>
          ))
        : null}
      <Box
        style={{
          border: "2px solid #434343",
          cursor: "pointer",
        }}
        clicked={state}
        name="i"
        onClick={() => setState(true)}
      >
        ?
      </Box>
      <Ellipsis>...</Ellipsis>
      <Box name="n">?</Box>
    </DPTableContainer>
  );
  return (
    <Container>
      <DPTitle> DP </DPTitle>
      <Header style={{ cursor: "default" }}>
        <Line>
          <EmSpan> - </EmSpan>
          {desc[0]}
        </Line>
        <Line>
          <EmSpan>DP[i] - </EmSpan>
          {desc[1]}
        </Line>
      </Header>
      <DPTitle> DP Table</DPTitle>
      {renderDPTale(baseTable, transferTable)}
      {transferTableOther ? renderDPTale(baseC, transferTableOther) : null}
      <DPTitle>Base Case</DPTitle>
      <Header clicked={!state} onClick={() => setState(false)}>
        {baseCase}
      </Header>
      <DPTitle></DPTitle>
      <Header clicked={state} onClick={() => setState(true)}>
        {typeof transferEquation === "string"
          ? transferEquation
          : transferEquation.map((str) => <Line>{str}</Line>)}
      </Header>
    </Container>
  );
};

export default DPExplain;
