/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import styled from "@emotion/styled";
import { Tag } from "antd";

const RegularTitleContainer = styled.div`
  padding: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 1.5rem;
  background: ${(props) => tagProps[props.type].background};
`;

const RegularTitleTag = styled(Tag)`
  border-radius: 0 0 5px 5px;
  position: absolute;
  top: 0;
  font-weight: bold;
`;

const RegularTitleText = styled.span`
  font-weight: bold;
  color: ${(props) => tagProps[props.type].color};
  margin-top: 20px;
`;

const tagProps = {
  note: { color: "#eb2f96", background: "#fff2e8", value: "Note" },
  example: { color: "#13c2c2", background: "#e6fffb", value: "" },
  summary: { color: "#722ed1", background: "#f9f0ff", value: "" },
  show: { color: "#1890ff", background: "#e6f7ff", value: "" },
  flow: { color: "#d48806", background: "#fffbe6", value: "" },
};

const RegularTitle = ({ type = "note", title }) => (
  <RegularTitleContainer type={type}>
    <RegularTitleTag color={tagProps[type].color}>
      {tagProps[type].value}
    </RegularTitleTag>
    {title ? <RegularTitleText type={type}>{title}</RegularTitleText> : null}
  </RegularTitleContainer>
);

export default RegularTitle;
