import React, { useEffect, useRef, useState } from "react";
import styled from "@emotion/styled";
import * as d3 from "d3";

const SVGContainer = styled.div`
  overflow-y: scroll;
  overflow-x: hidden;
`;
const SVGMain = styled.svg`
  transition: 0.5s all;
  transition-timing-function: ease-in;
`;

const outer = "M0 -100 L85 -50 L85 50 L0 100 L-85 50 L-85 -50 L0 -100";
const tri = "M0 -90 L75 50 L-75 50 L0 -90";

const transferData = (data) => {
  const bookGenres = ["", "", "", "lan", "hong", "", ""];
  const bookColor = [
    "#ffa940",
    "#ff85c0",
    "#9254de",
    "#597ef7",
    "#f5222d",
    "#ff4d4f",
    "#13c2c2",
  ];
  const bookTitleColor = [
    "#fff",
    "#fff",
    "#faad14",
    "#faad14",
    "#fff",
    "#fff",
    "#fff",
  ];
  const path = [
    "M0,100 C60,95 5,48 0,0 C-5,48 -60,95 0,100",
    "M-35 0 C-25 25 25 25 35 0 C50 25 25 75 0 100 C-25 75 -50 25 -35 0",
    "M0,0 C50,40 50,70 20,100 L0,80 L-20,100 C-50,70 -50,40 0,0",
    "M0 0 C50 25 50 75 0 100 C-50 75 -50 25 0 0",
  ];

  const colorScale = d3
    .scaleOrdinal()
    .domain(bookGenres)
    .range(bookColor)
    .unknown("#FFF2B4");

  const titleColorScale = d3
    .scaleOrdinal()
    .domain(bookGenres)
    .range(bookTitleColor)
    .unknown("#fff");

  const pathScale = d3.scaleOrdinal().domain(bookGenres).range(path);

  const coverPathScale = d3
    .scaleQuantize()
    .domain(d3.extent(data, (d) => d.pages))
    .range([5, 4, 3, 2, 1]);

  const sizeScale = d3.scaleLinear().domain([3, 10]).range([0.1, 0.6]);

  const numPetalScale = d3
    .scaleQuantize()
    .domain(d3.extent(data, (d) => d.votes))
    .range([5, 6, 7, 8, 9, 10]);

  return data.map((d) => {
    return {
      color: colorScale(d.genres[0]),
      path: pathScale(d.genres[0]),
      numPetal: numPetalScale(d.votes) + 1,
      size: sizeScale(d.rating),
      title: d.title,
      pages: coverPathScale(d.pages),
      titleColor: titleColorScale(d.genres[0]),
    };
  });
};

const throttle = (func, wait = 100) => {
  let lastTime = 0;
  return function (...args) {
    let now = +new Date();
    if (now - lastTime > wait) {
      lastTime = now;
      func.apply(this, args);
    }
  };
};

const BookFlower = ({ books }) => {
  const svgRef = useRef(null);
  const containerRef = useRef({});

  const [width, setWidth] = useState(500);
  const [data, setData] = useState(transferData(books));

  const pathWidth = 200,
    perRow = Math.floor(width / pathWidth);
  const height = (Math.ceil(books.length / perRow) + 0.5) * pathWidth;

  const calculateGridPos = (i) => {
    return [
      ((i % perRow) + 0.5) * pathWidth,
      (Math.floor(i / perRow) + 0.5) * pathWidth,
    ];
  };

  const drawSvg = (ref) => {
    const outerColor = "#434343";
    const mainColor = "#d9d9d9";
    const sideColor = "#b7eb8f";

    const scale = [0.7, 0.65, 0.6, 0.55, 0.5];
    const g = d3
      .select(ref.current)
      .selectAll("g")
      .data(data, (d) => d)
      .join("g")
      .attr("transform", (d, i) => `translate(${calculateGridPos(i)})`);

    g.append("path")
      .attr("d", outer)
      .attr("transform", `scale(0.9)`)
      .attr("fill", outerColor)
      .attr("stroke", "black")
      .attr("stroke-width", 2);

    g.append("path")
      .attr("d", outer)
      .attr("transform", `scale(0.8)`)
      .attr("fill", mainColor)
      .attr("stroke", "black")
      .attr("stroke-width", 5);

    g.selectAll("path.page")
      .data((d) =>
        new Array(d.pages).fill(0).map((_, i) => {
          return {
            scale: 0.8 - ((0.8 - scale[d.pages - 1]) / (d.pages + 1)) * (i + 1),
          };
        })
      )
      .enter()
      .append("path")
      .attr("d", outer)
      .attr("transform", (d) => `scale(${d.scale})`)
      .attr("fill", mainColor)
      .attr("stroke", "black")
      .attr("stroke-width", 1);

    g.append("path")
      .attr("d", outer)
      .attr("transform", (d) => `scale(${scale[d.pages - 1]})`)
      .attr("fill", "#262626")
      .attr("stroke", "black")
      .attr("stroke-width", 5);

    g.append("path")
      .attr("d", tri)
      .attr("transform", `scale(0.9)`)
      .attr("fill", sideColor)
      .attr("stroke", "black")
      .attr("stroke-width", 3);

    g.append("path")
      .attr("d", tri)
      .attr("transform", `scale(0.8)`)
      .attr("fill", sideColor)
      .attr("stroke", "black")
      .attr("stroke-width", 2);

    g.append("path")
      .attr("d", tri)
      .attr("transform", `scale(0.65)`)
      .attr("fill", "#262626");

    g.selectAll("path.flower")
      .data((d) =>
        new Array(d.numPetal * 2).fill(0).map((_, i) => {
          return {
            ...d,
            rotate: (i % d.numPetal) * (360 / d.numPetal),
            color: Math.floor(i / d.numPetal) === 0 ? "white" : d.color,
            opacity: Math.floor(i / d.numPetal) === 0 ? 1 : 0.7,
          };
        })
      )
      .enter()
      .append("path")
      .attr("transform", (d, i) => {
        return `rotate(${d.rotate || 0})scale(${d.size || 1})`;
      })
      .attr("d", (d) => d.path)
      .attr("fill", (d) => d.color)
      .attr("fill-opacity", (d) => d.opacity)
      .attr("stroke", (d) => d.color)
      .attr("stroke-width", 5);

    g.append("text")
      .attr("text-anchor", "middle")
      .attr("dy", ".35em")
      .style("font-size", "1em")
      .style("font-weight", "border")
      .attr("fill", (d) => d.titleColor)
      .text((d) => d.title)
      .attr("stroke", (d) => d.titleColor)
      .attr("stroke-width", 0.5);
  };

  const drawSvgThrottle = throttle(drawSvg, 500);

  useEffect(() => {
    setWidth(containerRef.current.offsetWidth);
    window.addEventListener("resize", () =>
      setWidth(containerRef.current.offsetWidth)
    );
    d3.select(svgRef.current).transition().duration(1000);
  }, []);

  useEffect(() => {
    drawSvgThrottle(svgRef);
  }, [width]);

  return (
    <SVGContainer ref={containerRef}>
      <SVGMain ref={svgRef} width={width} height={height}></SVGMain>
    </SVGContainer>
  );
};

export default BookFlower;
