/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "@emotion/react";
import styled from "@emotion/styled";

const CompareContainer = styled.div`
  width: 100%;
  margin: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const CompareTitle = styled.p`
  padding: 0 10px;
  margin: 20px 0;
  font-size: 20px;
  font-weight: bolder;
  border-left: 3px solid #ffccc7;
  border-radius: 3px;
`;

const CompareSubTitle = styled.div`
  margin: 15px 0;
  font-size: 16px;
  font-weight: bold;
`;

const CompareList = styled.ul`
  margin: 5px;
`;

const CompareListTag = styled.em`
  margin-right: 5px;
  color: black;
  background: ${(props) => (props.children === "pro" ? "#ffd6e7" : "#efdbff")};
  border-radius: 3px;
  padding: 0 5px;
`;

const CompareSummary = styled.p`
  margin: 15px 0;
  padding: 0 10px;
  background: #f6ffed;
  text-align: center;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
`;

const renderList = (desc, pro, con) => (
  <CompareList>
    {desc.map((item) => (
      <li>{item}</li>
    ))}
    {pro.map((item) => (
      <li>
        <CompareListTag>pro</CompareListTag>
        {item}
      </li>
    ))}
    {con.map((item) => (
      <li>
        <CompareListTag>con</CompareListTag>
        {item}
      </li>
    ))}
  </CompareList>
);

const Compare = ({
  against,
  againstDetail,
  sideADesc,
  sideBDesc,
  sideAPro,
  sideBPro,
  sideACon,
  sideBCon,
  summary,
}) => {
  return (
    <CompareContainer>
      <CompareTitle>
        {against[0]} vs. {against[1]}
      </CompareTitle>
      <CompareSubTitle>
         {againstDetail ? againstDetail[0] : against[0]}
      </CompareSubTitle>
      {renderList(sideADesc, sideAPro, sideACon)}
      <CompareSubTitle>
         {againstDetail ? againstDetail[1] : against[1]}
      </CompareSubTitle>
      {renderList(sideBDesc, sideBPro, sideBCon)}
      <CompareSummary>"{summary}"</CompareSummary>
    </CompareContainer>
  );
};

export default Compare;
